    line-height: 2rem;
}

a.info-description-link {
    font-weight: 400;
    color: #333 !important;
    .dark & {
        color: #d6d6e6 !important;
    }
}

.link-tilt a {
    .dark & {
        color: #80baff;
        transition: opacity 0.15s;
    }
    color: #127fff;
    transition: opacity 0.15s;
}

b {
    .dark & {
        font-weight: 500;
    }
}

.link-tilt a:hover {
    opacity: 0.5;
}

.top-story {
    width: 13rem;
}

#Packages {
    .linkItem {
        @apply inline-flex items-center;
    }
}


.svgIcon {
    .st0,
    svg {
        fill: currentColor;
        max-height: 40px;
        width: 100%;
        height: 100%;
    }
}

#Home .svgIcon {
    .st0,
    svg {
        fill: currentColor;
        max-height: 28px;
    }
}

h1 {
    @apply text-3xl font-extrabold;
}

h2 {
    @apply text-2xl;
}

h3 {
    @apply text-xl;
}

h3 {
    @apply text-lg;
}

.label:before {
    content: "";
    height: 0;
    top: 50%;
    transform: translate(-100%, -50%);
    position: absolute;
    width: 0;
    left: 0;
    z-index: 0;
    border: 5px solid transparent;
    border-right-color: transparent;
    border-right-color: inherit;
}

.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}
.lds-ellipsis div {
    position: absolute;
    top: 33px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #1F2937;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.dark {
    .lds-ellipsis div {
        background: #fff;
    }
}
.lds-ellipsis div:nth-child(1) {
    left: 8px;
    animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
    left: 8px;
    animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
    left: 32px;
    animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
    left: 56px;
    animation: lds-ellipsis3 0.6s infinite;
}
@keyframes lds-ellipsis1 {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}
@keyframes lds-ellipsis3 {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0);
    }
}
@keyframes lds-ellipsis2 {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(24px, 0);
    }
}
